.categorySk {
	margin-right: 12px;
	margin-bottom: 30px;
	animation: skeleton 1s ease infinite alternate;
}

@keyframes skeleton {
	to {
		opacity: 0.5;
	}
}

.categorySkImg {
	background-color: #999898;
	height: 125px;
	border-radius: 110px;
}

@media (max-width: 400px) {
	.categorySkImg {
		height: 100px;
	}
}
